import React, { Component } from "react";
import { Link } from "react-router-dom";
import head from "../assets/cabeza.png";
import "./Header.css";

class Header extends Component {
  render() {
    return (
      <div
        className={`${this.props.selectedSection !== "none"
          ? "ShortHeader"
          : ""} Header`}
      >
        <div className="photo">
          <img
            src={head}
            alt="Luis Revilla"
            style={{
              maxWidth: "100%",
              borderRadius: "100%",
              maxHeight: "100%"
            }}
          />
        </div>
        <div>
          <Link to="/" style={{ textDecoration: "none", color: "#ce3728" }}>
            <h1 className="myNameText">Luis Revilla</h1>
          </Link>
          <h3 className="introText" style={{ color: "#000000" }}>
            I used to write about sports. Now I write software.
          </h3>
        </div>
        <div className="btnRow">
          <Link className="linkToSection" to="/bio">
            <button
              onClick={this.chooseSection}
              className={`${this.props.selectedSection === "Bio"
                ? "selectedOuter"
                : ""} outer`}
            >
              <div
                id="bio"
                className={`${this.props.selectedSection === "Bio"
                  ? "selectedInner"
                  : ""} inner`}
              >
                Bio
              </div>
            </button>
          </Link>
          <Link className="linkToSection" to="/toolset">
            <button
              className={`${this.props.selectedSection === "Toolset"
                ? "selectedOuter"
                : ""} outer`}
              onClick={this.chooseSection}
            >
              <div
                id="toolset"
                className={`${this.props.selectedSection === "Toolset"
                  ? "selectedInner"
                  : ""} inner`}
              >
                Toolset
              </div>
            </button>
          </Link>
        </div>
        <div className="big-header-line" />
      </div>
    );
  }
}

export default Header;



