import "./Toolset.css";
import React from "react";
import { Link } from "react-router-dom";
import Header from "../Header/Header";
import { SectionTitle, Footer } from "../Reusable/Reusable";
import data from "../toolData";

const Toolcard = props => (
  <Link to={`toolset/${props.tool.id}`} className="grid-box">
    <img src={props.tool.logoPath} alt={props.tool.name} />
    <div className="grid-box-title">{props.tool.name}</div>
  </Link>
);

const Toolset = props => {
  return (
    <div className="toolset-page">
      <Header selectedSection={props.selectedSection} />
      <SectionTitle link="/toolset" title={props.selectedSection} />
      <div className="tool-grid">
        {data.map(t => <Toolcard key={t.id} tool={t} />)}
      </div>
      <Footer />
    </div>
  );
};
export default Toolset;



