import React from "react";
import Header from "../Header/Header";
import { SectionTitle, Footer } from "../Reusable/Reusable";
import "./ToolProjects.css";

const ProjectMeme = props => {
  return (
    <div>
      <h3 className="project-title">
        <span>{props.project.title}</span>
      </h3>
      <img
        style={{ width: "100%", border: "1px dashed #d5d5d5" }}
        src={props.project.img}
        alt={props.project.title}
      />
      <p className="project-usage">
        <span style={{ fontWeight: "bold" }}>Usage: </span>
        {props.project.usage}
        <br />

        <div style={{ marginTop: "8px" }}>
          <span style={{ fontWeight: "bold" }}>Leveraged features: </span>
          {props.project.leveraged}
        </div>
      </p>
    </div>
  );
};

const ToolProjects = props => {
  return (
    <div>
      <Header selectedSection="Toolset" />
      <SectionTitle
        link="/toolset"
        title={`${props.tool.name} projects`}
        tool={props.tool}
      />
      {props.tool.projects.map(proj => <ProjectMeme project={proj} />)}
      <Footer />
    </div>
  );
};

export default ToolProjects;



