import React from "react";
import { Link } from "react-router-dom";
import "./Reusable.css";
import arrow from "../assets/arrow.png";

export const SectionTitle = props => (
  <div className="sectionTitle">
    <Link to={props.link}>
      {props.tool ? (
        <div
          style={{
            width: "40px",
            height: "40px",
            display: "inline-block",
            position: "absolute",
            left: "0"
          }}
        >
          <img
            style={{
              width: "100%",
              height: "100%"
            }}
            src={arrow}
          />
        </div>
      ) : (
        ""
      )}
    </Link>
    <h2>{props.title}</h2>
  </div>
);

export const Footer = () => (
  <div className="contact-me">
    <h3>Let's get to work!</h3>
    <p>
      <span style={{ fontWeight: "bold" }}>Email:</span>&nbsp;
      <span>luisrevillameza@gmail.com</span>
    </p>
    <p>
      <span style={{ fontWeight: "bold" }}>Github:</span>&nbsp;
      <a
        style={{ textDecoration: "none", color: "#CE3728" }}
        href="https://github.com/LuisRevillaM"
        target="_blank"
      >
        https://github.com/LuisRevillaM
      </a>
    </p>
    <p style={{ marginBottom: "0" }}>
      <span style={{ fontWeight: "bold" }}>Twitter:</span>&nbsp;
      <a
        style={{ textDecoration: "none", color: "#CE3728" }}
        href="https://twitter.com/LuisRevilla"
        target="_blank"
      >
        https://twitter.com/LuisRevilla
      </a>
    </p>
  </div>
);



