import React, { Component } from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import Home from "./Home/Home";
import Toolset from "./Toolset/Toolset";
import Bio from "./Bio/Bio";
import data from "./toolData";
import ToolProjects from "./ToolProjects/ToolProjects";

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <Switch>
          <Route
            path="/"
            exact
            component={() => <Home selectedSection="none" />}
          />
          <Route
            path="/toolset"
            exact
            component={() => <Toolset selectedSection="Toolset" />}
          />
          <Route
            path="/bio"
            exact
            component={() => <Bio selectedSection="Bio" />}
          />
          <Route
            path="/toolset/:id"
            exact
            component={props => (
              <ToolProjects
                tool={data.find(t => t.id.toString() === props.match.params.id)}
              />
            )}
          />
        </Switch>
      </BrowserRouter>
    );
  }
}

export default App;



