import React from "react";
import Header from "../Header/Header";
import { SectionTitle, Footer } from "../Reusable/Reusable";
import "./Bio.css";
import born from "../assets/born.jpg";
import usb from "../assets/usb.jpg";
import up from "../assets/up.jpg";
import ivon from "../assets/ivon.jpg";
import grass from "../assets/grass.png";
import papeles from "../assets/papeles.jpg";
import amore from "../assets/amore.mp4";
import chapela from "../assets/chapela.jpg";
import cronistas from "../assets/cronistas.png";
import miseria from "../assets/miseria.jpg";
import argentina from "../assets/hinchada.jpg";
import swapig from "../assets/swapig.jpg";
import chile from "../assets/chile.png";
import yay from "../assets/yay.mp4";

const Bio = props => {
  return (
    <div>
      <Header selectedSection={props.selectedSection} />
      <SectionTitle link="/bio" title={props.selectedSection} />
      <div id="story">
        <h3>
          <span>
            I was born and raised in Bolívar, Venezuela's largest state.
          </span>
        </h3>
        <img src={born} className="bio-image" alt="Me in a park" />
        <p className="bio-p">
          Bolívar sits right above the equatorial line. It's really hot all
          year.
        </p>
        <h3>
          <span>
            My hometown is 160 miles north of the jungle portrayed in the Pixar
            movie "Up" (2009).
          </span>
        </h3>
        <img src={up} className="bio-image" alt="Up, a Pixar Movie" />
        <p className="bio-p">
          Dubbed "Paradise Falls" in the movie, "Angel Falls" is the tallest
          waterfall in the world.
        </p>
        <h3>
          <span>
            I took Electronic Engineering at the Simón Bolívar University, in
            Caracas.
          </span>
        </h3>
        <img src={usb} className="bio-image" alt="Simon Bolivar University" />
        <p className="bio-p">
          There, I got to write code in C and Assembly language. My first
          encounter with programming!
        </p>
        <h3>
          <span>
            In the end, however, my lifelong affair with sports got serious and
            I became a sportswriter. She was my first boss:
          </span>
        </h3>
        <img src={ivon} className="bio-image" alt="Ivon Gaete" />
        <p className="bio-p">
          Ivón Gaete is a well-known sports reporter in Venezuela. She ran the
          website where I started. She now works for the Major League Baseball
          site in spanish.
        </p>
        <h3>
          <span>The stadium was my office. I was in love with the grass.</span>
        </h3>
        <img
          src={papeles}
          className="bio-image"
          alt="Soccer stadium in Venezuela"
        />
        <img
          src={grass}
          id="grass"
          className="bio-image"
          alt="Baseball stadium in Venezuela"
        />
        <p className="bio-p">
          I got to interview some of our most prominent athletes, and witnessed
          little pieces of Venezuelan sports history, first-hand.
        </p>
        <h3>
          <span>
            Here you can see me running to capture a goal celebration.
          </span>
        </h3>
        <video src={amore} className="amore" loop="true" autoPlay />
        <p className="bio-p">
          The fan in me had to throw a fist in the air, though. That night
          Venezuela beat Argentina for the first time ever.
        </p>
        <h3>
          <span>
            Then I met Daniel Chapela, a true sportswriting legend in our
            country. He gave me a job after one conversation.
          </span>
        </h3>
        <img src={chapela} className="bio-image" alt="Daniel Chapela" />
        <img
          src={cronistas}
          id="cronistas"
          className="bio-image"
          alt="Radio show crew"
        />
        <p className="bio-p">
          I joined the production team of what was Venezuela's most prestigious
          sports radio show. Around a year after that, Chapela asked me (still a
          total outsider) to write the epilogue of his latest{" "}
          <a
            href="https://itunes.apple.com/us/book/vinotinto-f-c/id658907467?mt=11"
            style={{ textDecoration: "none", color: "CE3728" }}
          >
            book
          </a>. Big deal. One of my proudest pieces.
        </p>
        <h3>
          <span>
            In 2014, Venezuela was quickly becoming one of the most miserable
            places on Earth. So I joined the millions who have fled the country.
          </span>
        </h3>
        <img src={miseria} className="bio-image" alt="Food shortages" />
        <p className="bio-p">
          Scarcity of food and medicine. Rampant crime. The highest inflation in
          the world. Lack of true democracy. An unbearable cocktail.
        </p>
        <h3>
          <span>
            I came to Argentina and continued my career as a freelance writer.
          </span>
        </h3>
        <img
          src={argentina}
          className="bio-image"
          alt="Argentina soccer fans"
        />
        <p className="bio-p">
          I naturally fit here: Argies are as crazy about soccer as I am.
        </p>
        <h3>
          <span>
            Then a friend had an interesting idea: human economy consists mostly
            of reciprocal transactions…
          </span>
        </h3>
        <div className="reciprocity">
          <div className="party">A</div>
          <div className="arrow-container">
            <img
              id="reciprocal-arrow-1"
              src="https://openclipart.org/image/800px/svg_to_png/193052/arrowright.png"
            />
            <img
              id="reciprocal-arrow-2"
              src="https://openclipart.org/image/800px/svg_to_png/193052/arrowright.png"
            />
          </div>
          <div className="party">B</div>
        </div>
        <p className="reciprocity-explainer">
          Both parties give each other something.
        </p>
        <h3>
          <span>
            When one party doesn't want what the other offers, there's no
            transaction…
          </span>
        </h3>
        <div className="reciprocity">
          <div className="party">A</div>
          <div className="arrow-container">
            <img
              id="unrequited-arrow"
              src="https://openclipart.org/image/800px/svg_to_png/193052/arrowright.png"
            />
            <div id="equis">
              <div id="line1" />
              <div id="line2" />
            </div>
          </div>
          <div className="party">B</div>
        </div>
        <p className="reciprocity-explainer">B does not want what A offers.</p>
        <h3>
          <span>
            What if there's another party (C) trading something B wants, but
            interested in A's item?
          </span>
        </h3>
        <div id="multiparty">
          <div id="up-row">
            <div className="party">A</div>
            <div id="arrow">
              <img
                className="arrow1 right"
                src="https://openclipart.org/image/800px/svg_to_png/193052/arrowright.png"
              />
            </div>
            <div className="party">B</div>
          </div>
          <div className="bottom-row">
            <img
              className="arrow1 upleft"
              src="https://openclipart.org/image/800px/svg_to_png/193052/arrowright.png"
            />
            <div className="party third">C</div>
            <img
              className="two downleft"
              src="https://openclipart.org/image/800px/svg_to_png/193052/arrowright.png"
            />
          </div>
        </div>
        <p className="reciprocity-explainer">
          Multi-reciprocity between 3 parties. A "circular" transaction can be
          performed. Every party gets what they want from someone, and gives
          what they offer to someone else.
        </p>
        <h3>
          <span>
            The idea was Swapig: a platform that finds reciprocal and
            multi-reciprocal swap offers made by users, enabling more
            transactions than our 1-on-1 economy.
          </span>
        </h3>
        <img src={swapig} className="bio-image" />
        <p className="bio-p">
          I wanted to help make it real, and soon realized there was no better
          way to help than coding.
        </p>
        <h3>
          <span>
            I learned HTML, CSS and JQuery, built the client for a demo, wrote
            the application to an accelerator and we got accepted.
          </span>
        </h3>
        <img src={chile} className="bio-image" />
        <p className="bio-p">
          We were part of the 16th generation of Start-up Chile (2016).
        </p>
        <h3>
          <span>
            The project didn't become a sustainable company. But the process
            changed my life: I got hooked to programming for good!
          </span>
        </h3>
        <video src={yay} className="amore" loop="true" autoPlay />
        <p className="bio-p">
          I learned two programming languages, several frameworks, platforms,
          libraries… I learned how to learn. Software is my newfound passion.
        </p>
      </div>
      <Footer />
    </div>
  );
};
export default Bio;



